.header__burger_span {
    display: none;
  }
  
  @media (max-width: 767px) {
    .header__burger_span {
      display: block;
      background-color: #ffff;
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      top: 8px;
      transition: all 0.3s ease 0s;
    }
  
    .header__burger_span.active {
      transform: scale(0);
    }
  }