.popup{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  /*  display: none;*/
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, .5);
    display: flex;
    /* position: absolute; */
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
}
    