.header__item {
    width: 100%;
    margin: 0 24px 0 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
  }
  
  .header__item:last-of-type {
    margin-right: 0;
  }
  
  @media screen and (max-width: 767px) {
    .header__item {
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      margin: 40px auto 18px;
    }
    .header__item:last-of-type {
      margin-top: 0;
      margin-bottom: 40px;
    }
  
    .header__item.active {
      font-size: 18px;
      line-height: 22px;
    }
  }