.popup__icon {
    display: block;
    width: 120px;
    height: 120px;
  
    margin: 60px auto 32px;
  
    border: none;
    outline: none;
    box-sizing: border-box;
    background-color: transparent;
  }
  
  @media screen and (max-width: 767px) {
    .popup__icon {
      margin-top: 50px;
      margin-bottom: 40px;
    }
  }