.profile-info__name{
    color: white;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    padding: 0;
    max-width: 294px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
@media screen and (max-width: 600px){
    .profile-info__name{
        font-size: 27px;
        line-height: 33px;
        max-width: 189px;
    }
  }