.header__burger {
    display: none;
  }
  
  @media (max-width: 767px) {
    .header__burger {
      display: block;
      position: relative;
      width: 24px;
      height: 20px;
      z-index: 3;
    }
    .header__burger:before,
    .header__burger:after {
      content: "";
      background-color: #ffff;
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      transition: all 0.3s ease 0s;
    }
  
    .header__burger:before {
      top: 0;
    }
  
    .header__burger:after {
      bottom: 0;
    }
  
    .header__burger.active:before {
      transform: rotate(45deg);
      top: 9px;
    }
  
    .header__burger.active:after {
      transform: rotate(-45deg);
      bottom: 9px;
    }
  }