.header__menu {
    max-width: 100%;
    max-height: 22px;
    margin: 6px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    z-index: 3;
  }
  
  @media (max-width: 767px) {
    .header__menu {
      margin: 0 auto;
      position: fixed;
      top: -100%;
      left: 0;
      width: 100%;
      max-height: 142px;
      border-bottom: 1px solid rgba(84, 84, 84, 0.7);
      z-index: 1;
      background-color: var(--color__background);
      opacity: 0.8;
      display: block;
      transition: all 0.3s ease-in 0s;
    }
  
    .header__menu.active {
      top: 70px;
    }
  }